body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


html{

}
body{
  margin:0 auto;
}
.App {
   
  margin: auto;
  width:50%;
  box-sizing:border-box;
   
}

.myForm{
  width:100%;
  background-color:#eee;
  border-radius:5px;
  padding:20px;
  box-sizing:border-box;
   
}

.formField{
  width:100%;
  padding:12px 20px;
  display:block;
  border-radius: 5px;
  border:solid 1px #ccc;
  box-sizing:border-box;

}

.myButton{
  width:100%;
  background-color:#777;
   color:#fff;
   padding:14px 20px;
   margin:8px 0;
   border:none;
   border-radius:5px;
   cursor:pointer;
   box-sizing:border-box;
}

.myList{
  list-style:none;
  box-sizing:border-box;
}

.myListButton{
  display:inline-block;
  height:auto;
  width:auto;
  border-radius: 5px;
  border: 1px solid #ddd;
  margin:5px;
  box-sizing:border-box;
}
input,button{
  font-size:16px;
}
